.center-navbar {
    width: 100%;
    margin-left: 0%;
    margin-right: 0%;
  }

.row {
  width: 96%;
  max-width: 1020px;
  margin: auto;
}

.banner {
  display: inline-block;
  vertical-align: middle;
  margin: auto;
  width: 85%;
  padding-bottom: 30px;
  text-align: center;
}

.banner-text {
  width: 100%;
}

.banner-text h1 {
  color: black;
  letter-spacing: -2px;
  margin: auto;
  text-shadow: 0px, 1px, 3px, rgba(0, 0, 0, .8);
}

.social {
  margin: 10px,0,10px,0;
  padding: 0;
  font-size: 30px;
  text-shadow: 0px, 1px, 2px, rgba(0, 0, 0, .8);
}

ul {
  list-style-type: none;
}

li {
  display: inline;
}

li a:hover {
  background-color: black;
}

